    height: 16px;
    width: 16px;
    border: 3px solid grey;
    vertical-align: middle;
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all 300ms ease; }

.label--radio {
  cursor: pointer;
  font-size: 1.3em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0.25em; }
  .label--radio:before {
    content: "";
    display: block;
    height: 16px;
    width: 16px;
    border: 3px solid grey;
    vertical-align: middle;
    border-radius: 8px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all 300ms ease; }

.input {
  padding: 0.5em;
  border: 0;
  background-color: #eee;
  transition: background-color 300ms ease-in-out; }
  .input:focus {
    outline: 0;
    background-color: #ccc; }
  .input.full {
    display: block;
    width: 100%; }
  .input.breath {
    margin: 0.25em 0; }
  .input.btn-pair {
    border-radius: 0.5em 0 0 0.5em;
    padding: .75em; }

