  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center; }

.input--checkbox {
  display: none; }
  .input--checkbox:checked + .label--checkbox::before {
    content: "";
    font-size: 10px;
    background-color: grey; }

.input--radio {
  display: none; }
  .input--radio:checked + .label--radio::before {
    content: "";
    font-size: 10px;
    background-color: grey; }

.input--select {
  padding: 0.5em;
  border: 0;
  background-color: #eee;
  transition: background-color 300ms ease-in-out; }
  .input--select:focus {
    outline: 0;
    background-color: #ccc; }
  .input--select.full {
    display: block;
    width: 100%; }
  .input--select.breath {
