.alert {
  position: fixed;
  width: 80vw;
  min-width: 300px;
  padding: 1em;
  top: 2em;
  left: 50%;
  transform: translateX(-50%);
  z-index: 50;
  border-radius: 0.25em; }
  .alert .close {
    display: none;
    cursor: pointer;
    color: #697479;
    transition: all 300ms ease-in-out; }
    .alert .close:hover {
      color: #000; }
  .alert.hide {
    display: none; }

.alert--closeable {
  position: fixed;
  width: 80vw;
  min-width: 300px;
  padding: 1em;
  top: 2em;
  left: 50%;
  transform: translateX(-50%);
  z-index: 50;
  border-radius: 0.25em;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .alert--closeable .close {
    display: none;
    cursor: pointer;
    color: #697479;
    transition: all 300ms ease-in-out; }
    .alert--closeable .close:hover {
      color: #000; }
  .alert--closeable .close {
    display: block; }
  .alert--closeable.hide {
    display: none; }

.btn {
  margin: .1em 0;
  text-decoration: none;
  line-height: 1;
  padding: .75em 1.75em;
  width: auto;
  background-color: #697479;
  border: 0.1em solid #697479;
  border-radius: 0.25em;
  font-weight: 700;
  font-family: sans-serif;
  color: #fff;
  display: inline-block;
  cursor: pointer;
  transition: all 200ms ease-in-out; }
  .btn:hover {
    opacity: .7; }
  .btn.breath {
    margin: .25em; }
  .btn.hoverable:hover {
    background-color: #fff;
    border-color: #697479;
    color: #697479; }
  .btn.outline {
    background-color: #fff;
    border-color: #697479;
    color: #697479; }
