      background-color: #697479;
      color: #fff; }
  .btn.full {
    width: 100%;
    display: block;
    margin: .25em 0; }
  .btn.icon {
    padding: .75em; }
  .btn.round {
    padding: .75em;
    border-radius: 100%; }
  .btn.long {
    padding: .75em 5em; }
  .btn.primary {
    background-color: #084887;
    border-color: #084887;
    color: #fff; }
    .btn.primary.hoverable:hover {
